import Vue from 'vue'
import Router from 'vue-router'
import NProgress from 'nprogress'

import About from './views/About.vue'
import Education from './views/Education.vue'
import Experience from './views/Experience.vue'
import Skills from './views/Skills.vue'
import Achievement from './views/Achievement.vue'
import Contact from './views/Contact.vue'

Vue.use(Router)

const router = new Router({
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'about',
      meta:{title:'Hi, I\'m Arsan!'},
      component: About
    },
    {
      path: '/education',
      name: 'education',
      meta:{title:'My Education'},
      component: Education
    },
    {
      path: '/experience',
      name: 'experience',
      meta:{title:'My Experience'},
      component: Experience
    },
    {
      path: '/skills',
      name: 'skills',
      meta:{title:'My Skills'},
      component: Skills
    },
    {
      path: '/achievement',
      name: 'achievement',
      meta:{title:'My Achievement'},
      component: Achievement
    },
    {
      path: '/contact',
      name: 'contact',
      meta:{title:'Contact Me'},
      component: Contact
    }
  ]
})

router.beforeEach((to, from, next) => {
  document.title = to.meta.title // Set <title> for every components
  NProgress.start()
  NProgress.set(0.5)
  next()
})
router.afterEach(() => {
  setTimeout(() => NProgress.done(), 20)
})

export default router
